import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import SnackbarContent from '@material-ui/core/SnackbarContent';
import SimpleModalWrapped from './SimpleModal.js';

const styles = theme => ({
    root: {
        width: '90%',
        margin: '45px auto',
        overflowX: 'auto',
        maxWidth: '700px',
        minWidth: '700px',
    },
    table: {
        minWidth: 900,
    },
    row: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.background.default
        },
    },
});

let id = 0;
function createData(name, date, hour, played) {
    id += 1;
    return {id, name, date, hour, played};
}

const data = [
    createData('Chicharrones FC vs White Sharks', '2 - 2', 'stats', 'pass-game'),
    createData('Mean Machine vs Kronos', 'Agosto 23', '5:00 pm', 'next-game'),

    createData('Kronos vs Chicharrones FC', 'TBD', '0:00 pm', 'to-play'),
    createData('White Sharks vs Mean Machine', 'TBD', '0:00 pm', 'to-play'),

    createData('Mean Machine vs Chicharrones FC', 'TBD', '0:00 pm', 'to-play'),
    createData('White Sharks vs Kronos', 'TBD', '0:00 pm', 'to-play'),

    createData('Chicharrones FC vs Kronos', 'TBD', '0:00 pm', 'to-play'),
    createData('Mean Machine vs White Sharks', 'TBD', '0:00 pm', 'to-play'),

    createData('White Sharks vs Kronos', 'TBD', '0:00 pm', 'to-play'),
    createData('Chicharrones FC vs Mean Machine', 'TBD', '0:00 pm', 'to-play'),

    createData('Chicharrones FC vs Mean Machine', 'TBD', '0:00 pm', 'to-play'),
    createData('Kronos vs White Sharks', 'TBD', '6:00 pm', 'to-play'),

];

class CalendarComponent extends Component {

    state = {
        openModal: false,
    };

    handleOpen = () => {
        this.setState({openModal: true});
    };

    handleClose = () => {
        this.setState({openModal: false});
    };

    render() {
        const {classes} = this.props;

        return (
            <div className="calendar-div">
                {data.map(n => {
                    return (
                        <SnackbarContent className={classes.snackbar + ' ' + n.played}
                                         message={
                                             <div>
                                                 <span className="math-span">{n.name}</span>
                                                 <span className="math-date">{n.date}</span>
                                                 <span className="math-hour"
                                                 onClick={this.handleOpen}>{n.hour}</span>
                                             </div>
                                         }/>
                    );
                })}
                <SimpleModalWrapped open={this.state.openModal} handleClose={this.handleClose}/>
            </div>
        );
    }
}

CalendarComponent.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CalendarComponent)


